@import url('https://fonts.googleapis.com/css2?family=Kanit:wght@400;500;600&family=Lobster&family=Poppins:wght@100;200;300;400;500;600;700&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: #272525;
  font-weight: 200;
  font-family: 'Poppins', sans-serif;
}

body{
  background-color: #fff;
}

.title-logo{
  color: #fff;
  font-family: 'Kanit', sans-serif;
}

.mid-section{
  min-height: 60vh;
}

//home
.welcome-panel{
  height: 300px;
  overflow: hidden;
  width: 100%;
  position: relative;
}

.welcome-panel #welcome-image{
  width: 100%;
  height: 100%;
  z-index: -1;
  filter: brightness(50%);
  object-fit: cover;
}

.welcome-panel .welcome-msg{
  position: absolute;
  left: 0;
  top: 0;
  padding-top: 60px;
  width: 100%;
  height: 100%;
  display: flex;
  gap: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.welcome-panel .welcome-msg h3, .welcome-panel .welcome-msg p{
  color: #fff;
}

.welcome-panel .welcome-msg #shop-now{
  padding: 5px 20px;
}