.navbar{
    width: 100%;
    display: flex;
    align-items: center;
    padding: 5px 20px;
    height: 45px;
    background-color: black;
    justify-content: space-between;
}

.navbar .tabs{
    list-style: none;
    display: flex;
    gap: 20px;
    cursor: pointer;
    align-items: center;
    justify-content: flex-end;
    padding-right: 40px;
}

.navbar .nav{
    text-decoration: none;
    color: #fff;
}



.navbar .tab{
    transition: all .3s;
}

.navbar .tab:hover{
    border-bottom: 2px solid #FF3366;
}

.navbar .tabs .active{
    border-bottom: 1px solid #FF3366;
}

// footer section
.footer{
    display: grid;
    justify-content: center;
    align-items: center;
    padding: 20px 60px;
    grid-template-columns: 1fr 1fr;
    grid-template-areas: 
        'header header'
        'content1 content2'
    ;
    background-color: black;
}

.footer .social-contacts{
    grid-area: header;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    padding: 10px;
}

.footer .social-sites{
    grid-area: content1;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.footer .social-groups{
    grid-area: content2;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.footer p, .footer input, .footer a{
    color: #fff;
    text-decoration: none;
    font-weight: 100;
}

.footer p{
    font-family: 'Times New Roman', Times, serif;
    font-size: 20px;
}

.footer input{
    color: #272525;
    font-weight: 300;
    outline: none;
    border: none;
}

.footer form{
    width: 100%;
    padding: 10px 0 ;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5px;
}

.footer #email{
    width: 60%;
    font-size: 14px;
    padding: 5.5px 10px;
    border-radius: 4px 0 0 4px;
}

.footer #sub{
    padding: 6px 20px;
    cursor: pointer;
    color: #fff;
    letter-spacing: 1px;
    font-weight: 500;
    background-color: #FF3366;
}

.footer a{
    width: 200px;
}